export interface GetAllTransferAction {
  type: ActionTypes.GET_ALL_TRANSFER;
  payload: TransferRequestType[] | null;
}

export interface GetAllLeavesAction {
  type: ActionTypes.GET_ALL_LEAVES;
  payload: TransferLeaveType[] | null;
}

export interface GetMyTransferAction {
  type: ActionTypes.GET_MY_TRANSFER;
  payload: RequestedTransfer[] | null;
}

export interface GetQualificationAction {
  type: ActionTypes.GET_QUALIFICATION;
  payload: Qualifications[] | null;
}

export interface UploadSignedDocumentAction {
  type: ActionTypes.UPLOAD_SIGNED_DOCUMENT;
  payload: {
    teacher_transfer_id: string;
    mayor_letter: string;
  };
}

