export type FT_GetAllTransfer = (
  district_id: string,
  callBack: (
    status: boolean,
    data: TransferRequestType[] | null,
    msg: string
  ) => void
) => void;
export const FC_GetAllTransfers = (
  district_id: string,
  callBack: (
    status: boolean,
    data: TransferRequestType[] | null,
    msg: string
  ) => void
) => {
  callBack(true, null, "");
  return async (dispatch: Dispatch) => {
    try {
      dispatch<GetAllTransferAction>({
        type: ActionTypes.GET_ALL_TRANSFER,
        payload: [],
      });
      setAxiosToken();
      const res = await axios.get<TransferRequestType[]>(
        `${APP_URL}/teachertransfers/requesteddde/${district_id}`
      );
      // console.log("====================================");
      // console.log({ getAllTransfers: res });
      // console.log("====================================");

      const needed_data = res.data.filter(
        (item) => item.requested_status === "APPROVED"
      );

      // * save data
      dispatch<GetAllTransferAction>({
        type: ActionTypes.GET_ALL_TRANSFER,
        payload: needed_data,
      });
      callBack(false, needed_data, "");
    } catch (error) {
      // console.log(" ----- ------  ------");

      // console.log(error);
      callBack(false, null, errorToText(error));
      //   callBack(
      //     false,
      //     errorToText(error, () => {})
      //   );
    }
  };
};

